import {
	getUserAddressList
} from '@/api/userApi.js'
export default {
	data() {
		return {
			list: [

			],
			from: ''
		};
	},
	onLoad(options) {
		this.from = options.from || ''
		// 获取用户地址列表
		this.getUserAddresList()
	},
	onShow() {
		// 获取用户地址列表
		this.getUserAddresList()
	},
	methods: {
		// 返回订单页面
		goBlack(item) {
			if (this.from === 'submit') {
				uni.setStorageSync('address', JSON.stringify(item))
				uni.navigateBack({
					delta: 1
				})
			}
		},
		// 获取用户地址列表
		getUserAddresList() {
			getUserAddressList().then(res => {
				if (res.code === '1000') {
					this.list = res.data
				}
			}).catch(err => {
				uni.showToast(err.message || '地址列表获取错误')
			})
		},
		goAddedit(id) {
			let url = id != 0 ? '/pages/user/addedit/addedit?id=' + id : '/pages/user/addedit/addedit'
			uni.navigateTo({
				url
			})
		}
	}
}
